.screen__container {
  height: 100vh;
  width: 100vw;
  max-width: 100vw;
  max-height: 100vh;
  margin: 0;
  overflow: hidden;
  transform-origin: 50vw 50vh;
  transform: translate(-130vw, 0);
  will-change: transform;
}

.screen {
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: 2;
  display: flex;
  .posbutton {
    position: absolute;
    bottom: 10px;
    flex-grow: 2;
  }
  @media screen and (max-width: 800px) {
    flex-direction: column;
    height: auto;
  }
  @media screen and (max-width: 600px) {
    width: 100%;
    height: 100vh;
  }
}

.screen--inverse {
  .screen__video {
    order: 2;
  }
  .screen__infos {
    order: 1;
  }
  .screen__nav {
    right: unset;
    left: 2.5vw;
    @media screen and (max-width: 800px) {
      right: 4vw;
      left: unset;
    }
  }
}

.screen__nav {
  position: absolute;
  right: 2.5vw;
  top: 50%;
  transform: translateY(-50%);
  @media screen and (max-width: 800px) {
    right: 4vw;
    top: 35%;
  }
}

.screen__video {
  height: 93%;
  width: auto;
  @media screen and (max-width: 800px) {
    order: 2;
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  @media screen and (max-width: 500px) {
    margin-bottom: 50px;
    height: auto;
    width: 90%;
    margin-left: 10%;
  }
}

.screen__video--big {
  display: block;
  @media screen and (max-width: 800px) {
    display: none;
  }
}

.screen__video--small {
  display: none;
  @media screen and (max-width: 800px) {
    display: block;
  }
}

.screen__infos {
  // background-color: black;
  height: 100%;
  display: flex;
  flex-grow: 2;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  @media screen and (max-width: 800px) {
    order: 1;
  }
}

.screen__title {
  width: 379px;
  font-size: 40px;
  margin-bottom: 28px;
  line-height: 1;
  @media screen and (max-width: 800px) {
    width: 80vw;
    // font-size: 9vw;
    margin-bottom: 24px;
  }
  @media screen and (max-width: 500px) {
    margin-bottom: 20px;
    font-size: 28px;
    line-height: 98%;
  }
}

.screen__title--bigger {
  width: 430px;
  @media screen and (max-width: 500px) {
    width: 70vw;
  }
}

.screen--1 {
  left: 130vw;
  top: 0;
}

.screen--2 {
  left: 15vw;
  top: 115vh;
  .screen__deco {
    pointer-events: none;
    position: absolute;
    width: 100vw;
    left: -30%;
    bottom: 28vh;
  }
  // @media screen and (max-width: 600px) {
  //   top: 100vh;
  //   left: 235vw;
  // }
}

.screen--3 {
  left: 155vw;
  top: 230vh;
  @media screen and (max-width: 800px) {
    .screen__title {
      width: 68vw;
    }
  }
  @media screen and (max-width: 600px) {
    top: 180vh;
    left: 235vw;
  }
}

.screen--4 {
  left: 40vw;

  top: 360vh;
  @media screen and (max-width: 800px) {
    top: 320vh;
    .screen__title {
      width: 68vw;
    }
  }
  @media screen and (max-width: 600px) {
    left: 30vw;
    top: 310vh;
  }
}

.screen--5 {
  left: 30vw;
  top: 575vh;
  .screen__deco {
    position: absolute;
    width: 100vw;
    left: 0%;
    bottom: -10vh;
  }
  @media screen and (max-width: 800px) {
    top: 485vh;
    .screen__nav {
      left: unset;
      right: 4vw;
      top: 40%;
    }
    .screen__video {
      display: none;
    }
  }
  @media screen and (max-width: 600px) {
    top: 480vh;
    .screen__deco {
      position: absolute;
      width: 110vw;
      left: 0;
      bottom: -3vh;
    }
  }
}

.screen--end {
  background-color: black;
  position: absolute !important;
  opacity: 0;
  pointer-events: none;
  .intro__title {
    @media screen and (max-width: 600px) {
      width: 80vw;
      font-size: 30px;
    }
  }
}
