.button {
  margin-bottom: 12px;
  text-align: center;
  text-transform: uppercase;
  font-size: 0.75rem;
  font-weight: normal;
  line-height: 1;
  letter-spacing: 0.1vw;
  transition: all 0.3s ease-in-out;
  &:last-of-type {
    margin-bottom: 0px;
  }
  &.is-active {
    background-color: white;
    color: black;
  }

  input[type="radio"] {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
    transition: all 0.3s ease-in-out;
  }
  input[type="radio"] + label:hover {
    background-color: white;
    color: black;
  }
  input[type="radio"]:checked + label {
    background-color: white;
    color: black;
  }
  @media screen and (max-width: 800px) {
    // font-size: 3.3vw;
    // margin-bottom: 30px;
  }
  @media screen and (max-width: 600px) {
    // font-size: 3vw;
  }
  @media screen and (max-width: 500px) {
    margin-bottom: 9px;
    // font-size: 3.5vw;
  }
  :focus {
    outline: none;
    box-shadow: none;
  }
}

.button__txt {
  cursor: pointer;
  display: block;
  padding: 16px;
  background-color: transparent;
  border: 1px solid white;
  color: white;
  transition: all 0.3s ease-in-out;
  
  &:hover {
    background-color: white;
    color: black;
  }
  @media screen and (max-width: 800px) {
    padding: 16px;
  }

  @media screen and (max-width: 600px) {
    padding: 12px;
  }
}

.button--gold {
  color: black;
  border: none;
  background: center / cover no-repeat;
  background-image: url("../img/gold.png");
  background-color: $gold;
  line-height: 1.3;

  .button__txt {
    line-height: 1.3;
    background-color: transparent;
    border: 2px solid transparent;
    color: black;
    width: 100%;
    height: 100%;
    text-transform: uppercase;
    font-size: 1vw;
    @media screen and (max-width: 800px) {
      font-size: 3.3vw;
      &:last-of-type {
        margin-bottom: 0vh;
      }
    }
  }
  @media screen and (max-width: 800px) {
    &:last-of-type {
      margin-bottom: 0vh;
    }
  }
}

.button--white {
  color: black;
  background-color: white;

  .button__txt {
    color: black;
    background-color: white;
    &:hover {
      color: white;
      background-color: black;
    }
  }
  &:hover {
    color: white;
    background-color: black;
  }
}
