@keyframes rotateBallBig {
  from {
    transform: rotateZ(-5deg);
  }

  to {
    transform: rotateZ(5deg);
  }
}
@keyframes rotateBallMiddle {
  from {
    transform: rotateZ(3deg);
  }

  to {
    transform: rotateZ(-3deg);
  }
}
@keyframes rotateBallSmall {
  from {
    transform: rotateZ(-2deg);
  }

  to {
    transform: rotateZ(2deg);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes levitate {
  0% {
    transform: translate(0px, 5vh);
  }
  25% {
    transform: translate(5vw, 0);
  }
  50% {
    transform: translate(0, -5vh);
  }
  75% {
    transform: translate(-5vw, -0vh);
  }
  100% {
    transform: translate(0px, 5vh);
  }
}

@keyframes float2 {
	0% {
		transform: rotateZ(180deg) translateY(0px);
	}
	50% {
		transform: rotateZ(180deg) translateY(-20px);
	}
	100% {
		transform: rotateZ(180deg) translateY(0px);
	}
}

@keyframes float {
	0% {
		transform: translateY(0px);
	}
	50% {
		transform: translateY(-20px);
	}
	100% {
		transform: translateY(0px);
	}
}