.form {
  display: flex;
  flex-direction: column;
  width: 240px;
  @media screen and (max-width: 800px) {
    width: 40vw;
    margin-bottom: 15vh;
  }
  @media screen and (max-width: 500px) {
    width: 60vw;
    margin-bottom: 10vh;
    width: 160px;
  }
}

.form--range {
  width: 380px;
  @-moz-document url-prefix() {
    width: 388px;
  }

  @media screen and (max-width: 800px) {
    // width: 60vw;
  }
  @media screen and (max-width: 500px) {
    width: 270px;
  }
}

.form__input {
  width: 100%;
}

.form__range-container {
  position: relative;
  margin: 7vh 0 8vh;
  @media screen and (max-width: 800px) {
    margin: 4vh 0 7vh;
  }
}

.form__range-hide {
  position: absolute;
  height: 10px;
  width: 15px;
  background-color: #000;
  pointer-events: none;

}

.form__range-hide--left {
  left: 0px;
  top: 8px;
  @media screen and (max-width: 800px) {
    left: 0;
  }
  @-moz-document url-prefix() {
    left: 0px;
    width: 15px;

    // width: 10px;
  }
}

.form__range-hide--right {
  right: -4px;
  top: 8px;
  @media screen and (max-width: 800px) {
    width: 20px;
  }
  @-moz-document url-prefix() {
    right: -5px;
    @media screen and (max-width: 800px) {
      right: -3px;
    }
  }
}

.form__range-stop {
  position: absolute;
  width: 8px;
  height: 8px;
  z-index: 10;
  background-color: white;
  border-radius: 50%;
  pointer-events: none;
  @media screen and (max-width: 800px) {
    width: 15px;
    height: 15px;
  }
  @media screen and (max-width: 800px) {
    width: 10px;
    height: 10px;
  }
}

.form__range-stop--min {
  top: 10px;
  left: 7px;
  @media screen and (max-width: 800px) {
    top: 9px;
    left: 14px;
  }
  @media screen and (max-width: 600px) {
    top: 9px;
    left: 14.5px;
  }
  @-moz-document url-prefix() {
    top: 9.5px;
    left: 12px;
  }
}

.form__range-stop--middle {
  top: 10px;
  left: 49.9%;
  transform: translateX(-50%);
  // left: 49.5%;
  @media screen and (max-width: 800px) {
    top: 9px;
    left: 50%;
    transform: translateX(-50%);
    // left: 49.1%;
  }
  @media screen and (max-width: 600px) {
    top: 9px;
  }
  @-moz-document url-prefix() {
    left: 195px;
    top: 9.5px;
    @media screen and (max-width: 600px) {
      left: 136px;
    }
    // left: 50%;
    // transform: translateX(-50%);
  }
}

.form__range-stop--max {
  top: 10px;
  right: 7px;
  @media screen and (max-width: 800px) {
    top: 9px;
    right: 10px;
  }
  @media screen and (max-width: 600px) {
    top: 9px;
    right: 15px;
  }
  @-moz-document url-prefix() {
    top: 9.5px;
    right: 9px;
    @media screen and (max-width: 600px) {
      right: 11px;
    }
  }
}

.form__range {
  cursor: grab;
  width: 100%;
  border: none;
  box-shadow: none;
  color: white;
  -webkit-appearance: none;
  margin: 0;
  height: 2px;
  border-radius: 2px;
  &:focus {
    outline: none;
    box-shadow: none;
  }
  @media screen and (max-width: 800px) {
    height: 3px;
  }

  &::-moz-range-track {
    margin-top: 2px;
    height: 2px;
    border-radius: 2px;
    background-color: white;
    @media screen and (max-width: 800px) {
      height: 3px;
    }
  }
  &::-webkit-slider-runnable-track {
    margin-top: 2px;
    height: 2px;
    border-radius: 2px;
    background-color: white;
    @media screen and (max-width: 800px) {
      height: 3px;
    }
  }
  &::-moz-range-thumb {
    z-index: 2;
    position: relative;
    transform-origin: 50% 50%;
    cursor: grab;
    -webkit-appearance: none;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    background: #000000;
    border: 2px solid #ffffff;
    transform: scale(1.2);
    transition: transform 0.3s cubic-bezier(0, 0.62, 0.31, 1.2);
    @media screen and (max-width: 800px) {
      height: 40px;
      width: 40px;
      border: 3px solid #ffffff;
    }
    @media screen and (max-width: 600px) {
      height: 40px;
      width: 40px;
      border: 3px solid #ffffff;
    }
  }
  &::-webkit-slider-thumb {
    cursor: grab;
    -webkit-appearance: none;
    height: 23px;
    width: 23px;
    border-radius: 50%;
    background: #000000;
    border: 2px solid #ffffff;
    transform: translateY(-50%);
    transition: transform 0.3s cubic-bezier(0, 0.62, 0.31, 1.2);
    @media screen and (max-width: 800px) {
      height: 40px;
      width: 40px;
      border: 3px solid #ffffff;
    }
    @media screen and (max-width: 600px) {
      height: 40px;
      width: 40px;
      border: 3px solid #ffffff;
    }
  }
  &:hover {
    &::-moz-range-thumb {
      cursor: drag;
      transition: transform 0.3s cubic-bezier(0, 0.62, 0.31, 1.2);
      transform: scale(1.8);
    }
    &::-webkit-slider-thumb {
      cursor: drag;
      transition: transform 0.3s cubic-bezier(0, 0.62, 0.31, 1.2);
      transform: translateY(-50%) scale(1.8);
    }
  }
}

.form__label {
  font-size: 1rem;
  text-transform: uppercase;
