.decoration {
  position: absolute;
  pointer-events: none;
}

.decoration--ruban {
  height: 235vh;
  top: -22vh;
  left: 200vh;
  transform: translateX(-100%) rotateY(0deg) rotate(10deg);
  @media screen and (max-width: 800px) {
    left: 86vh;
  }
  @media screen and (max-width: 600px) {
    width: 195vw;
    top: -22vh;
    left: 112vw;
  }
}
.decoration--boule-big {
  height: 200vh;
  top: -20vh;
  left: 191vw;
  transform-origin: top;
  animation: 5s rotateBallBig  0s infinite alternate forwards cubic-bezier(.36,.01,.4,1);

  @media screen and (max-width: 800px) {
    height: unset;
    width: 91vw;
    top: -19vh;
    left: 194vw;
  }
}

.decoration--star {
  width: 25vw;
  top: 126vh;
  left: 124vw;
  @media screen and (max-width: 600px) {
    top: 165vh;
    left: 135vw;
    width: 70vw;
  }
}

.decoration--confettis-1 {
  width: 41vw;
  top: 166vh;
  left: 114vw;
  @media screen and (max-width: 600px) {
    width: 50vw;
    top: 230vh;
    left: 114vw;
  }
}

.decoration--tube {
  width: 70vw;
  top: 320vh;
  left: 235vw;
  transform: translateX(-100%);
  @media screen and (max-width: 600px) {
    width: 130vw;
    top: 280vh;
    left: 295vw;
    transform: translateX(-100%);
  }
}

.decoration--confettis-2 {
  width: 60vw;
  top: 330vh;
  left: 15vw;
  @media screen and (max-width: 800px) {
    display: none;
  }
}

.decoration--confettis-3 {
  width: 50vw;
  top: 490vh;
  left: 15vw;
  @media screen and (max-width: 800px) {
    top: 440vh;
  }
  @media screen and (max-width: 600px) {
    width: 10vw;
  }
}

.decoration--flacon {
  width: 26vw;
  top: 452vh;
  left: 75vw;
  @media screen and (max-width: 800px) {
    width: 46vw;
    top: 419vh;
    left: 75vw;
  }
  @media screen and (max-width: 600px) {
    width: 62vw;
    top: 409vh;
    left: 75vw;
  }
}

.decoration--confettis-4 {
  width: 55vw;
  top: 480vh;
  left: 85vw;
  @media screen and (max-width: 800px) {
    width: 55vw;
    top: 470vh;
    left: 105vw;
  }
  @media screen and (max-width: 600px) {
    width: 101vw;
    top: 440vh;
    left: 25vw;
    transform: rotateZ(-62deg);
  }
}
