.footer__bloc {
  position: relative;
  min-height: 80vh;
  &:nth-of-type(2n + 1) {
    grid-column: 1 / 5;
    .footer__content {
      position: absolute;
      // width: 19vw;
      width: 270px;
      right: 5vw;
      text-align: center;
      @media screen and (max-width: 1100px) {
        bottom: 18vh;
      }
      @media screen and (max-width: 800px) {
        bottom: 11vh;
      }
      @media screen and (max-width: 600px) {
        width: 50vw;
        bottom: 16vh;
        right: 1vw;
        .footer__title {
          width: 30vw;
        }
      }
    }
    @media screen and (max-width: 600px) {
      grid-column: 1 / 3;
      grid-row: 1 / 2;
    }
  }
  &:nth-of-type(2n) {
    grid-column: 5 / 9;
    .footer__content {
      position: absolute;
      width: 19vw;
      right: 10vw;
      text-align: center;
      @media screen and (max-width: 1100px) {
        bottom: 16vh;
      }
      @media screen and (max-width: 800px) {
        bottom: 10vh;
      }
      @media screen and (max-width: 600px) {
        width: 40vw;
        right: 6vw;
      }
    }
    .footer__btn {
      margin: 0 auto;
    }
    @media screen and (max-width: 600px) {
      grid-column: 1 / 3;
      grid-row: 2 / 3;
    }
  }
  @media screen and (max-width: 1100px) {
    min-height: 50vh;
  }
  @media screen and (max-width: 800px) {
    min-height: 30vh;
  }
  @media screen and (max-width: 600px) {
    min-height: 50vh;
  }
}

.footer__img {
  position: absolute;
  // width: 85%;
  height: 80vh;
  width: auto;
  bottom: 0;
  @media screen and (max-width: 1100px) {
    height: 50vh;
  }
  @media screen and (max-width: 800px) {
    height: 30vh;
  }
  @media screen and (max-width: 600px) {
    height: 50vh;
  }
}

.footer__content {
  position: absolute;
  bottom: 27vh;
  text-align: center;
}

.footer__title {
  font-size: 2.2em;
  line-height: 1;
  letter-spacing: 1px;
  margin-bottom: 20px;
  @media screen and (max-width: 800px) {
    font-size: 1.8em;
  }
  @media screen and (max-width: 600px) {
    font-size: 7vw;
    margin: 0 auto 15px;
  }
}

.footer__btn {
  width: 9vw;
  margin: 0 auto;
  padding: 16px;
  font-size: 0.75rem;
  border: 1px solid #fff;
  @media screen and (max-width: 600px) {
    width: 30vw;
    font-size: 2.9vw;
    padding: 14px;
  }
}
