.products__section {
  position: relative;
  overflow: hidden;
  padding: 10vh 0 20vh;
  background-color: white;
  color: black;

  img {
    max-width: 100%;
  }
  @media screen and (max-width: 800px) {
    padding: 5vh 0 9vh;
  }
  @media screen and (max-width: 600px) {
    padding: 6vh 0 9vh;
  }
}

.products__deco {
  pointer-events: none;
  position: absolute;
  @media screen and (max-width: 600px) {
    display: none;
  }
}

.products__deco--1 {
  right: 0;
  top: -2vw;
  width: 11.5vw;
}

.products__deco--2 {
  bottom: -25vw;
  left: 0;
  width: 15vw;
}

.products__title {
  margin: 0 auto;
  font-size: 2.5em;
  text-align: center;
  @media screen and (max-width: 600px) {
    font-size: 1.8rem;
  }
}

.products {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 24px;
  margin: 40px auto 0;
  @media screen and (max-width: 600px) {
    width: 90%;
    grid-template-columns: repeat(1, 1fr);
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;

    margin: 30px auto 0;
  }
}

.products__img-container {
  position: relative;
  display: block;
}

.products__cadre {
  width: 100%;
}

.products__img {
  // position: absolute;
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.products__surtitle {
  letter-spacing: 1px;
  font-size: 0.75rem;
  line-height: 0.75rem;
  margin-top: 20px;
  color: $lightGrey;
  text-transform: uppercase;
  @media screen and (max-width: 600px) {
    margin-top: 10px;
    font-size: 0.5rem;
    letter-spacing: 1px;
    line-height: 0.625rem;
  }
}

.products__product-title {
  font-size: 0.875rem;

  margin: 5px 0 6px;
  @media screen and (max-width: 600px) {
    font-size: 0.75rem;
    line-height: 1.1;
  }
}

.products__subtitle {
  color: #626262;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1;
  font-size: 0.625rem;
  margin-top: 10px;
  margin-bottom: 8px;
  @media screen and (max-width: 600px) {
    margin-top: 10px;
    margin-bottom: 32px;
    font-size: 0.5rem;
    letter-spacing: 1px;
    line-height: 1;
  }
}

.products__btn.products__btn {
  width: 35%;
  padding: 13px 16px;
  margin-top: 17px;
  background-color: black;
  border: 1px solid black;
  &:hover {
    background-color: white;
  }
  @media screen and (max-width: 800px) {
    width: 70%;
  }
  @media screen and (max-width: 600px) {
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 26px;
    letter-spacing: 0.83px;
    font-size: 0.625rem;
  }
}
