.intro {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // background-color: black;
}

.intro__title {
  color: $gold;
  font-size: 4.1vw;
  margin-bottom: 40px;
  @media screen and (max-width: 800px) {
    width: 70vw;
    text-align: center;
    font-size: 9.1vw;
    line-height: 1;
    margin-bottom: 60px;
  }
  @media screen and (max-width: 500px) {
    font-size: 14.1vw;
    // line-height: 1.2;
    margin-bottom: 34px;
  }
}

.intro__button {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  cursor: pointer;
  img {
    width: 100%;
    height: auto;
  }
}

.intro__decoration--confettis {
  left: -12vw;
  top: 0vw;
  width: 85vw;
  animation: float 15s ease-in-out infinite forwards;

  @media screen and (max-width: 800px) {
    left: 0vw;
    top: -0.4vw;
    width: 130vw;
  }
}

.intro__decoration--confettis2 {
  animation: float2 15s ease-in-out infinite reverse forwards;
  left: unset;
  right: 5vw;
  top: -5vw;
  width: 90vw;
  @media screen and (max-width: 800px) {
    right: 0vw;
    top: -0.4vw;
    width: 130vw;
  }
}

.intro__decoration--boule-l {
  left: 6vw;
  top: -1vh;
  width: 32vh;
  transform-origin: top;
  animation: 3s rotateBallBig 0s infinite alternate forwards cubic-bezier(0.36, 0.01, 0.4, 1);
  @media screen and (max-width: 800px) {
    left: -20vw;
    top: 7vh;
    width: 30vh;
  }
  @media screen and (max-width: 500px) {
    left: -27vw;
    top: 7vh;
    width: 30vh;
  }
}

.intro__decoration--boule-m {
  right: 5vw;
  top: 0vh;
  width: 22vh;
  transform-origin: top;
  animation: 2.5s rotateBallMiddle 0.1s infinite alternate forwards cubic-bezier(0.36, 0.01, 0.4, 1);

  @media screen and (max-width: 800px) {
    right: -3vw;
    top: -19vw;
    width: 18vh;
  }
  @media screen and (max-width: 500px) {
    right: 3vw;
    top: -29vw;
    width: 17vh;
  }
}

.intro__decoration--boule-s {
  right: 23vw;
  top: -0vh;
  width: 16vh;
  transform-origin: top;
  animation: 1.8s rotateBallSmall 0.1s infinite alternate forwards cubic-bezier(0.36, 0.01, 0.4, 1);
  @media screen and (max-width: 800px) {
    top: -3vh;
    width: 14vh;
  }
  @media screen and (max-width: 500px) {
    top: -3vh;
    width: 13vh;
    right: 31vw;
  }
}
