html {
  margin: 0;
}

body {
  margin: 0;
  background-color: black;
  color: white;
}

.result {
  // width: 100vw;
  // overflow: hidden;
  width: 100vw;
  height: auto;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4 {
  font-weight: normal;
  margin-block-start: 0;
  margin-block-end: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  list-style: none;
}

button {
  border: none;
  box-shadow: none;
}

a {
  text-decoration: none;
}

.desktop-hidden {
  display: none;
  @media screen and (max-width: 800px) {
    display: block;
  }
}
