.hero {
  min-height: 100vh;
  padding: 5vh 0;
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
  grid-column: 1 / 9;
  font-size: 1.4vw;
  align-items: center;

  @media screen and (max-width: 1100px) {
    min-height: unset;
    padding: 17vh 0 13vh;
  }
  @media screen and (max-width: 800px) {
    min-height: unset;
    padding: 12vh 0 10vh;
  }
  @media screen and (max-width: 600px) {
    width: 90%;
    grid-column: 1 / 3;
    overflow: visible;
  }
}

.hero__scroll {
  font-size: 12px;
  text-transform: uppercase;
  color: #bcbcbc;
  position: absolute;
  left: 50%;
  bottom: 30px;
  &:after {
    content: "";
    position: absolute;
    background-color: #bcbcbc;
    top: calc(100%+3px);
    left: 0;
    width: 1px;
    height: 27px;
  }
  @media screen and (max-width: 600px) {
    display: none;
  }
}

.hero__deco {
  position: absolute;
  @media screen and (max-width: 600px) {
    display: none;
  }
}

.hero__deco--1 {
  width: 30vw;
  top: 5vh;
  left: 6vw;
  @media screen and (max-width: 600px) {
    display: none;
  }
}

.hero__deco--2 {
  width: 28vw;
  top: 0;
  right: 6vw;
  @media screen and (max-width: 600px) {
    width: 124vw;
    top: 88vh;
    right: -79vw;
    z-index: 12;
  }
}

.hero__img {
  opacity: 0;
  grid-column: 2 / 5;
  margin-bottom: 50px;
  img {
    width: 60%;
    margin-left: 15%;
  }
  @media screen and (max-width: 1100px) {
    img {
      width: 78%;
      margin-left: 0;
    }
  }
  @media screen and (max-width: 800px) {
    img {
      transform: translateX(-10%);
      width: 100%;
      margin-left: 0;
    }
  }
  @media screen and (max-width: 600px) {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    img {
      transform: translateX(0%);
      width: 84%;
      margin-left: 8%;
    }
  }
}

.hero__infos {
  opacity: 0;
  grid-column: 5 / 8;
  @media screen and (max-width: 1100px) {
    margin-top: 0px;
  }
  @media screen and (max-width: 800px) {
    margin-top: 0px;
  }
  @media screen and (max-width: 600px) {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
  }
}

.hero__subtitle {
  opacity: 0;
  margin: 0;
  color: $grey;
  text-transform: uppercase;
  font-size: 1.2rem;
  line-height: 1;
  margin-bottom: 10px;
  @media screen and (max-width: 1290px) {
    font-size: 0.75rem;
    line-height: 0.75rem;
  }
}

.hero__title {
  opacity: 0;
  font-size: 3.5rem;
  margin: 10px 0 15px;
  @media screen and (max-width: 1290px) {
    font-size: 3rem;
  }
}

.hero__price {
  opacity: 0;
  font-size: 1.5rem;
  margin: 25px 0;
  @media screen and (max-width: 1290px) {
    font-size: 0.875rem;
  }
  @media screen and (max-width: 600px) {
    margin: 15px 0;
  }
}

.hero__desc {
  opacity: 0;
  width: 92%;
  font-size: 0.9rem;
  line-height: 1.7;
  @media screen and (max-width: 1290px) {
    font-size: 0.875rem;
    line-height: 1.375rem;
  }
  @media screen and (max-width: 1100px) {
    width: 96%;
  }
  @media screen and (max-width: 800px) {
    width: 96%;
  }
  @media screen and (max-width: 600px) {
    width: 90%;
    line-height: 1.6;
  }
}

.hero__btns {
  margin-top: 35px;
  display: flex;
  @media screen and (max-width: 850px) {
    align-items: flex-start;
    flex-direction: column;
  }
  @media screen and (max-width: 600px) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.hero__btn {
  position: relative;
  min-width: 30%;
  margin-right: 15px;
  margin-bottom: 0px;

  &:nth-child(2n) {
    margin-right: 0px;
  }
  @media screen and (max-width: 850px) {
    width: 170px;

    &:first-of-type {
      margin-right: 0px;
      margin-bottom: 15px;
    }
  }

  @media screen and (max-width: 600px) {
    width: 48%;

    &:first-of-type {
      margin-right: 15px;
      margin-bottom: 0px;
    }
  }
}

.hero__btn-mask {
  transform: scaleX(0);
  transform-origin: right;
  position: absolute;
  background-color: black;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
}
