@font-face {
    font-family: 'Paco';
    src: url('../fonts/Paco.eot');
    src: url('../fonts/Paco.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Paco.woff2') format('woff2'),
        url('../fonts/Paco.woff') format('woff'),
        url('../fonts/Paco.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Paco';
    src: url('../fonts/Paco-Italic.eot');
    src: url('../fonts/Paco-Italic.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Paco-Italic.woff2') format('woff2'),
        url('../fonts/Paco-Italic.woff') format('woff'),
        url('../fonts/Paco-Italic.ttf') format('truetype');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

body {
    font-family: 'Paco';
}